$output-bourbon-deprecation-warnings: false;
@import "bourbon";
@import "vars";

@function persent($px, $base: $base-width) {
	@return $px/$base*100 + .0%; }

@function vw($px, $base: 1920) {
	@return $px/$base*100 + vw; }

@function vh($px, $base: 972) {
	@return $px/$base*100 + vh; }

$base-color: #171717;
$border-color: #d5dbdf;
$error-color: #ff0024;

.fancybox-close {
	background: url(../img/fancybox-close.png) no-repeat center; }

.fancybox {

	&-skin {
		border-radius: 0; }

	&-close {
		@include size(30px);
		position: absolute;
		top: 50px;
		right: 55px;
		text-decoration: none;
		display: flex;
		align-items: center;
		justify-content: center;

		&:after {
			//content: "+"
			color: #000;
			font-weight: 300;
			font-size: 40px;
			display: block;
			transform: rotate(45deg); } } }


.order-form__input {

	&[required] {

		+ .order-form__label:after {
			content: "*";
			color: red;
			display: inline-block; } } }


.forms__input_error {

	~ .forms__error-message {
		display: block; }
	+ .checkbox__label span {
		color: red; } }

.forms {
	display: none;
	max-width: 1015px;
	box-sizing: border-box;
	// padding: 50px 87px 75px
	background: #fff;

	&__title {
		font-size: rem(30);
		text-align: center;
		font-family: "Proxima Nova", sans-serif;
		font-weight: 300;
		margin-bottom: 55px;
		color: #000; }

	&-header {
		border-bottom: 1px solid $border-color; }

	&-text-cont {
		display: flex;
		justify-content: center;
		font-size: rem(14);
		line-height: rem(20);
		letter-spasing: 0.04rem; }

	&__text {
		margin: vh(25) 0;
		max-width: 540px;
		text-align: center;
		font-size: rem(14);
		line-height: rem(20); }

	&-wrap {
		display: flex;
		justify-content: space-between;
		flex-wrap: wrap;

		&--bot {

			> div {
				//width: 50%
				box-sizing: border-box;

				&:nth-child(odd) {
					padding-right: 15px; }

				&:nth-child(even) {
					padding-left: 15px; } } } }

	&-input-cont {
		display: flex;
		flex-direction: column-reverse;
		box-sizing: border-box;
		width: 47.2%;
		min-width: 10px;
		position: relative;
		margin-bottom: vh(30);

		&--req {
			justify-content: space-between;
			align-items: flex-start; }

		&:nth-child(odd) {
 }			//padding-right: vw(12)

		&:nth-child(even) {
 }			//padding-left: vw(12)

		&--file, &--textarea {
			width: 100%;
			padding: 0 !important; }

		&--file {
			position: relative;

			&.forms__input_error {

				~ input[type="text"] {
					border-color: $error-color; } } } }

	&__input {
		box-sizing: border-box;
		@include size(100% 49px);
		border: 1px solid $border-color;
		border-radius: 1px;
		padding: 0 vw(17);
		outline: none;

		&_error {
			border-color: $error-color;

			+ .forms__checkbox-label {
				color: $error-color; } }

		&[required] + .forms__label:after {
			content: "*";
			color: red; }


		&--select {
			-webkit-appearance: none;
			background: url("../img/ico-select-arrow.png") center right 15px no-repeat, #f4f4f4;
			color: #777777; }

		&--file {
			display: none;

			&-support {
				width: 77.34%; } }

		&--textarea {
			max-width: 100%;
			height: vh(142);
			padding: {
				top: 5px;
				bottom: 5px; }
			max-height: 180px; } }

	&__file-formats {
		order: -1;
		margin-top: 15px;
		font-size: rem(14);
		color: #777777; }

	&__label {
		margin-bottom: 13px;

		&--file {
			order: 1;
			pointer-events: none;

			&:before {
				content: attr(data-btn-text);
				position: absolute;
				pointer-events: all;
				background: url("../img/ico-view.png") center left 15px no-repeat, #f4f4f4;
				right: 0;
				top: 33px;
				cursor: pointer;
				@include size(18.57% 49px);
				text-align: right;
				line-height: 49px;
				box-sizing: border-box;
				padding-right: vw(16); } } }

	&-checkbox {
		position: relative;

		&:before {
			content: "";
			position: absolute;
			top: 3px;
			left: 0;
			@include size(23px);
			//border: 1px solid $border-color
			border-radius: 1px;
			background: #fff; } }

	&__checkbox {
		opacity: 0;
		position: absolute;

		&:checked + label {

			&:before, &:after {
				opacity: 1; } }

		&-label {
			position: relative;
			z-index: 2;
			font-size: rem(16);
			line-height: rem(20);
			padding-left: 45px;
			cursor: pointer;
			height: 32px;
			display: flex;
			span {
				align-self: center; }

			&:before, &:after {
				content: "";
				position: absolute;
				background: #e31e24;
				transition: opacity .5s ease;
				opacity: 0; }

			&:before {
				@include size(10px 2px);
				top: 11px;
				left: 5px;
				transform-origin: left;
				transform: rotate(45deg); }

			&:after {
				@include size(10px 2px);
				top: 11px;
				left: 8px;
				transform-origin: right;
				transform: rotate(-45deg); } } }

	&__req-text {
		text-align: right;

		&:before {
			content: "* ";
			color: red;
			font-size: rem(14); } }

	&-submit-cont {
		display: flex;
		justify-content: center;
		margin-top: vh(40); }

	&__submit {
		color: #fff;
		border: 0;
		cursor: pointer;
		// background: $main-color
		font-size: rem(18);
		padding: vh(12) vw(40); }

	&__error {

		&-message {
			border: 1px solid #d4d4d4;
			background: #fff;
			color: #000;
			padding: 7px 10px;
			border-radius: 7px;
			position: absolute;
			top: 0;
			right: -6px;
			box-shadow: 0px 0px 21px 0px rgba(0, 0, 0, 0.1);
			display: none;

			&:after {
				content: "";
				background: url("../img/error-triangle.png") no-repeat;
				position: absolute;
				top: calc(100% - 0px);
				right: 19px;
				@include size(15px 8px); }

			&--file {

				.forms__error-message-icon {
					display: none; } }

			&-icon {
				display: block;
				border-radius: 50%;
				position: absolute;
				border: 1px solid $error-color;
				@include size(19px);
				background: #fff;
				top: 44px;
				right: 16px;

				&:after, &:before {
					content: "";
					position: absolute;
					width: 1px;
					background: $error-color;
					left: calc(50% - 1px); }

				&:before {
					top: 4px;
					height: 8px; }

				&:after {
					bottom: 4px;
					height: 2px; } } } }
